// src/components/FlowBuilder.js
import React, { useState, useCallback } from 'react';
import ReactFlow, { addEdge, Background, Controls, MiniMap } from 'react-flow-renderer';
import NodesPanel from './NodesPanel';
import SettingsPanel from './SettingsPanel';
import './FlowBuilder';

const initialElements = [];

const FlowBuilder = () => {
  const [elements, setElements] = useState(initialElements);
  const [selectedElement, setSelectedElement] = useState(null);

  const onConnect = (params) => setElements((els) => addEdge(params, els));

  const onElementClick = (event, element) => {
    setSelectedElement(element);
  };

  const updateNodeText = (id, text) => {
    setElements((els) =>
      els.map((el) => {
        if (el.id === id) {
          el.data = { ...el.data, label: text };
        }
        return el;
      })
    );
  };

  const onSave = () => {
    // Validate the flow and show errors if necessary
    const hasEmptyTargetHandles = elements.some(
      (el) => el.source && !elements.find((e) => e.target === el.source)
    );
    if (hasEmptyTargetHandles) {
      alert('Error: More than one node has empty target handles');
    } else {
      console.log('Flow saved:', elements);
    }
  };

  return (
    <div className="flow-builder">
      <NodesPanel setElements={setElements} />
      {selectedElement && (
        <SettingsPanel element={selectedElement} updateNodeText={updateNodeText} />
      )}
      <ReactFlow
        elements={elements}
        onConnect={onConnect}
        onElementClick={onElementClick}
        style={{ width: '100%', height: '90vh' }}
      >
        <MiniMap />
        <Controls />
        <Background />
      </ReactFlow>
      <button onClick={onSave}>Save Flow</button>
    </div>
  );
};

export default FlowBuilder;
