// src/components/NodesPanel.js
import React from 'react';
import  { useDrag }  from 'react-dnd';

const NodesPanel = ({ setElements }) => {
  const [{ isDragging }, drag] = useDrag(() => ({
    type: 'node',
    item: { id: '1', type: 'textNode' },
    collect: (monitor) => ({
      isDragging: !!monitor.isDragging(),
    }),
  }));

  const onDrop = (event) => {
    const nodeId = Math.random().toString();
    const newNode = {
      id: nodeId,
      type: 'default',
      position: { x: event.clientX, y: event.clientY },
      data: { label: 'New Node' },
    };
    setElements((els) => els.concat(newNode));
  };

  return (
    <div className="nodes-panel" onDrop={onDrop} onDragOver={(e) => e.preventDefault()}>
      <div ref={drag} className={`draggable-node ${isDragging ? 'dragging' : ''}`}>
        Text Node
      </div>
    </div>
  );
};

export default NodesPanel;
